/*form*/
.form {
    position : relative;
    width  : 740px;
    height : 498px;
    margin : 0 auto;
    padding: 1em;
    box-sizing: border-box;
    background-image: url(https://profunda-analytics.com/wp-content/uploads/2017/07/Contact-Form-background.jpg);
    text-align: left;
  }
  .form button {
    margin     : 10px 10px;
    font         : bold .6em sans-serif;
    border       : 2px solid #333;
    border-radius: 5px;
    cursor       : pointer;
  }